
button.icon {
  background-color: #fff;
  border-style: none;
  font-size: 1em;
  height: 1.5em;
  width: 1.5em;
  line-height: 1.25em;
  border-radius: 50%;
  padding: 0;
  margin: 0;
  color: #1aad8d;
}
button.big {
  margin-bottom: 1em;
}
button.big span {
  font-size: 2em;
}
.mini {
  font-size: 0.75em;
}
.title {
  color: #1aad8d;
  font-weight: bold;
}
table {
  border-collapse: collapse;
}
tr {
  border-bottom: solid 1px #d3d3d3;
}
th {
  border-bottom: solid 2px #d3d3d3;
}
th,
td {
  padding: 0 0.5em;
  text-align: left;
}
.mini-list,
ul.list {
  display: inline-block;
  list-style: none;
}
.notification {
  position: absolute;
  bottom: 7em;
  right: 3em;
  z-index: 100;
  width: auto;
  padding: 1em 3em 1em 2em;
  border-radius: 0.5em;
  box-shadow: 1px 1px 2px rgba(0,0,0,0.7);
  border: #1aad8d solid 2px;
  background-color: #fff;
  box-shadow: 2px 2px 4px rgba(0,0,0,0.8);
}
.notification .mini-list {
  max-height: 10em;
  overflow-y: scroll;
  overflow-x: hidden;
}


