
.node #fill {
  fill: #37e1bc;
}
.node.selected #fill {
  fill: #dfc899;
}
body {
  overflow-x: hidden;
}
#example,
.net,
button {
  margin: 0;
  padding: 0;
}
.net {
  background-color: #c4d4d0;
}
.net-svg {
  fill: #c4d4d0;
}
#example {
  position: absolute;
  max-width: 100%;
  width: 100%;
  height: calc(100% - 4px);
  top: 0;
  left: 0;
  bottom: 0;
  box-sizing: content-box;
}
button.menu {
  width: 1.5em;
  height: 1.5em;
  padding: 0;
  font-size: 2em;
  font-weight: bold;
  color: #1aad8d;
  border: #1aad8d solid 2px;
  box-shadow: 1px 1px 2px rgba(0,0,0,0.5);
}
button.menu:hover {
  color: #caa455;
  border-color: #caa455;
}
.circle {
  width: 4em;
  height: 4em;
  font-weight: bold;
  border-radius: 50%;
  border: #1aad8d solid 2px;
}
.connected {
  color: #1aad8d;
}
.disconnected {
  color: #be385d;
}
.node.nodeodd #fill,
.node.nodeodd {
  fill: #f00;
}
.node-label.odd {
  fill: #f00;
}
.over {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 100;
  padding: 1em;
}
.menu {
  position: relative;
  display: inline-block;
  padding-right: 3em;
  border-radius: 0.25em;
}
.options {
  padding: 0.5em 2em;
  border-radius: 0.5em;
  text-align: center;
  margin-bottom: 2em;
}
.close {
  display: block;
}
.close:after {
  content: "âœ–";
  position: absolute;
  right: 1em;
  top: 0.5em;
  font-size: 1.25em;
  color: #1aad8d;
  font-family: sans-serif;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.5);
}
.close:hover:after {
  color: #127862;
}
ul.inline {
  display: inline;
  margin: 0;
  padding: 0;
  color: #fff;
}
.inline {
  list-style: none;
}
.inline li {
  display: inline-block;
}
.inline li:after {
  content: '/';
  margin: 0 0.5em;
}
.sym-pointer:after {
  content: $sym-pointer;
}
.sym-kill:after {
  content: $sym-kill;
}
.sym-parent:after {
  content: $sym-parent;
}
.cross-cursor {
  cursor: crosshair;
}
.tools {
  position: absolute;
  bottom: 3em;
  right: 4em;
  z-index: 101;
  text-align: center;
}
.tools ul {
  list-style: none;
  margin: 0 3em 0.5em 0;
  padding: 0;
}
.tools ul li {
  display: inline;
  margin-left: 0.5em;
}
.tools ul button {
  width: 3em;
  height: 3em;
  padding: 0;
}
.tools ul button:hover {
  border-color: #caa455;
}
.tools ul button span {
  font-size: 2.5em;
  line-height: 1em;
  color: #1aad8d;
}
.tools ul button span:hover {
  color: #caa455;
}
.tools .selected {
  border-color: #caa455;
}
.tools .selected span {
  color: #caa455;
}
.tip {
  margin-right: 1em;
  font-style: italic;
  font-size: 0.8em;
  color: #fff;
}
.menu-net {
  background-color: #acd2ca;
  padding: 0.5em 1em;
  border: #127862 solid 2px;
  border-width: 6px 2px 3px;
  position: relative;
  margin-bottom: 2em;
  border-radius: 0.5em;
}
.menu-net .close {
  position: absolute;
  top: 0;
  right: 0;
}
.menu-net .close:after {
  color: #127862;
}
.menu-net .close:hover::after {
  color: #1aad8d;
}
.title {
  border: 1.5px #dcfaf3;
  border-style: dotted none;
  padding: 0.5em 0;
  margin: 1.5em 1em 0.5em 0.5em;
}
.title h1 {
  color: #dcfaf3;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.5);
}
.toaster,
.dialog {
  position: absolute;
  bottom: 0.5em;
  right: 2em;
  z-index: 500;
  background-color: #fff;
  border: #1aad8d solid 2px;
  border-radius: 0.25em;
  min-width: 20em;
  box-shadow: 1px 1px 2px rgba(0,0,0,0.5);
  -webkit-animation-name: toaster-anim;
          animation-name: toaster-anim;
  -webkit-animation-duration: 0.25s;
          animation-duration: 0.25s;
}
.dialog-container {
  position: absolute;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  top: 0;
  left: 0;
  min-height: 100%;
  min-width: 100%;
  border: #f00 solid 1px;
  background-color: rgba(0,0,0,0.3);
}
.dialog-container .dialog {
  width: 20em;
  min-height: 10em;
  position: relative;
  margin: auto;
}
.dialog-container .dialog input[type='radio'],
.dialog-container .dialog label {
  display: inline;
}
.dialog-container .dialog label {
  font-weight: bold;
}
.dialog-container .dialog .buttons {
  margin-top: 1em;
}
.dialog-container .dialog .btn {
  margin-left: 1em;
}
h2.hint {
  display: inline;
  position: absolute;
  margin-left: 1em;
  color: #f5fffc;
  font-size: 1em;
  font-style: italic;
  letter-spacing: 0.125em;
  text-shadow: 1px 1px 5px rgba(0,0,0,0.5), 2px 2px 15px #caa455;
  opacity: 0;
  -webkit-animation-name: hint-anim;
          animation-name: hint-anim;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}
h2.hint .icon {
  font-size: 3em;
  line-height: 0.5em;
}
.anim-button {
  -webkit-animation-name: button-anim;
          animation-name: button-anim;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}
@-webkit-keyframes toaster-anim {
0% {
    opacity: 0;
    -webkit-transform: scaleY(0) translateY(5em);
            transform: scaleY(0) translateY(5em);
}
100% {
    opacity: 1;
    -webkit-transform: scaleY(1) translateY(0);
            transform: scaleY(1) translateY(0);
}
}
@keyframes toaster-anim {
0% {
    opacity: 0;
    -webkit-transform: scaleY(0) translateY(5em);
            transform: scaleY(0) translateY(5em);
}
100% {
    opacity: 1;
    -webkit-transform: scaleY(1) translateY(0);
            transform: scaleY(1) translateY(0);
}
}
@-webkit-keyframes hint-anim {
0% {
    opacity: 0;
    -webkit-transform: translateX(6em);
            transform: translateX(6em);
    letter-spacing: 0.125em;
}
40% {
    opacity: 1;
    -webkit-transform: translateX(-1.5em);
            transform: translateX(-1.5em);
}
50% {
    -webkit-transform: translateX(-0.1em);
            transform: translateX(-0.1em);
    letter-spacing: 0.5em;
}
70% {
    opacity: 1;
    -webkit-transform: translateX(-0.5em);
            transform: translateX(-0.5em);
}
100% {
    opacity: 0;
}
}
@keyframes hint-anim {
0% {
    opacity: 0;
    -webkit-transform: translateX(6em);
            transform: translateX(6em);
    letter-spacing: 0.125em;
}
40% {
    opacity: 1;
    -webkit-transform: translateX(-1.5em);
            transform: translateX(-1.5em);
}
50% {
    -webkit-transform: translateX(-0.1em);
            transform: translateX(-0.1em);
    letter-spacing: 0.5em;
}
70% {
    opacity: 1;
    -webkit-transform: translateX(-0.5em);
            transform: translateX(-0.5em);
}
100% {
    opacity: 0;
}
}
@-webkit-keyframes button-anim {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
6% {
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
}
15% {
    -webkit-transform: rotate(10deg) scale(0.9, 0.9);
            transform: rotate(10deg) scale(0.9, 0.9);
}
20% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
}
35% {
    -webkit-transform: rotate(2deg) scale(1, 1);
            transform: rotate(2deg) scale(1, 1);
    box-shadow: 2px 2px 10px rgba(202,164,85,0.8);
    color: #caa455;
    border-color: #caa455;
}
50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
}
@keyframes button-anim {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
6% {
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
}
15% {
    -webkit-transform: rotate(10deg) scale(0.9, 0.9);
            transform: rotate(10deg) scale(0.9, 0.9);
}
20% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
}
35% {
    -webkit-transform: rotate(2deg) scale(1, 1);
            transform: rotate(2deg) scale(1, 1);
    box-shadow: 2px 2px 10px rgba(202,164,85,0.8);
    color: #caa455;
    border-color: #caa455;
}
50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
}


